<template>
    <div class="box">
        <p-header>确认支付</p-header>
        <div style="height: 0.9rem"></div>
        <div v-if="isshow">
            <div class="show-price">
               ￥ {{this.price}}
            </div>
            <div class="tips">请输入密码</div>
            <van-password-input
                    :value="value"
                    :length="6"
                    @focus="show = true"
            />
            <van-number-keyboard
                    v-model="value"
                    :show="show"
                    :maxlength="6"
                    @blur="show = false"
            />
        </div>
        <div v-if="issuccess">
            <div class="success">
                <img src="../../assets/success.png" alt="支付成功">
            </div>
            <div class="m-tittle">
                {{this.countDown}}秒后自动去我的详情
            </div>
            <div style="height: 1rem"></div>
            <div class="btn-all">
                <van-button type="default" @click="beforeIndex">返回首页</van-button>
                <van-button type="default" @click="beforeList">继续逛逛</van-button>
            </div>
        </div>
    </div>
</template>

<script>
    import PHeader from '../../components/p-header/p-header';
    let time=null;
    export default {
        name: "Pay",
        data() {
            return {
                value: '',
                show: true,
                price: 0,
                isshow: true,
                issuccess: false,
                countDown:5,
            }
        },
        methods: {
            beforeIndex(){
                clearInterval(time);
                this.$router.push({name:'index'});
            },
            beforeList(){
                clearInterval(time);
                this.$router.push({name:'classify'});
            }
        },
        components: {
            PHeader
        },
        watch: {
            value() {
                if (this.value === '123123') {
                    this.show = false;
                    this.isshow = false;
                    this.issuccess = true;
                    let timethis=this;
                     time=setInterval(function () {
                        timethis.countDown--;
                        if (timethis.countDown===0){
                            clearInterval(time);
                            timethis.$router.push({name:'mine'});
                        }
                    },1000)
                } else {
                    console.log('密码错误');
                }
            }
        },
        beforeMount() {
            this.price = this.$route.query.price;
        }
    }
</script>

<style scoped>
    .box {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.01);
    }
    .show-price{
        width: 100%;
        height: 2rem;
        line-height: 2rem;
        font-size: 0.4rem;
        color: red;
        text-align: center;
    }
    .tips{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        font-size: 0.32rem;
        color: #DDDDDD;
        text-align: center;
    }
    .success {
        padding-top: 0.6rem;
        box-sizing: border-box;
        width: 5rem;
        height: 5rem;
        margin: 0 auto;
        line-height: 5rem;
        text-align: center;
    }
    .btn-all{
        width: 100%;
        height: 0.9rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .van-button{
        margin: 0 0.12rem;
        border-radius: 0.15rem;
    }
    .van-button--default{
        color: #fff;
        background-color: #ee9100;
        border: 1px solid #ee9100;
    }
    .m-tittle{
        width: 100%;
        text-align: center;
        line-height: 0.6rem;
    }
</style>
